import React from 'react';
import { Container, Button, Row, Col, Card, CardBody, CardTitle } from 'reactstrap';
import cardTemplate from '../../assets/images/bank-card.svg';
const cardStyle = {
	lineHeight: '0'
};

class Home extends React.Component {
	state = {
		hasCard: false
	};

	renderCard() {
		return this.state.hasCard ? (
			<img className="" src={cardTemplate} style={cardStyle} alt="Seu cartão" height="160" />
		) : (
			<Card className="not-received-card">
				<CardBody>
					<CardTitle className="section-card-title">
						<i className="" /> Você não recebeu seu cartão?
					</CardTitle>
					<Row className="align-items-center">
						<Col xs="12" className="mb-2 text-justify">
							Para desbloquear seu cartão é simples. Basta clicar no botão abaixo, preencher as
							informações solicitados e pronto. Seu cartão está pronto para ser utilizado.
						</Col>
						<Col xs="12" className="mt-2">
							<Button color="primary">Quero ativar meu cartão</Button>
						</Col>
					</Row>
				</CardBody>
			</Card>
		);
	}
	render() {
		return (
			<div>
				{/*--------------------------------------------------------------------------------*/}
				{/* 1st Row                                                                        */}
				{/*--------------------------------------------------------------------------------*/}
				<Container fluid className="home-component w-auto border ">
					<Card id="saldo">
						<CardBody>
							<Row className="justify-content-around">
								<Col xs="12" className="">
									<div className="d-flex flex-wrap">
										<div>
											<CardTitle className="section-card-title">
												<i className="icon-wallet" /> Seu Saldo
											</CardTitle>
										</div>
									</div>
								</Col>
								<Col xs="auto" className="text-center">
									<Card className="balance-card">
										<CardBody>
											<Row className="align-items-center">
												<Col xs="12" className="mb-2">
													<span>Saldo atualizado as 10:45</span>
												</Col>
												<Col xs="12" className="my-2">
													<span className="display-6 text-success my-auto">R$ 35,28</span>
												</Col>
												<Col xs="12" className="mt-2">
													<span className="text-muted">Gasto no mês: R$ 300,45</span>
												</Col>
											</Row>
										</CardBody>
									</Card>
								</Col>
								<Col xs="auto" className="text-center mb-3">
									{this.renderCard()}
								</Col>
							</Row>
						</CardBody>
					</Card>
				</Container>
			</div>
		);
	}
}

export default Home;



